import React from 'react'

const Contact_option = ({ icon , name, mail, href }) => {
  return (
    <article className='contact-option'>
      <i className={icon} />
      <h4>{name}</h4>
      <h5>{mail}</h5>
      <a href={href} target="_blank">Send a message</a>
    </article>
  )
}

export default Contact_option