import React from 'react'
import { Swiper, SwiperSlide } from 'swiper/react';
import { Pagination } from "swiper";
import Testimonial from './Testimonial'
import AVT1 from '../../Assets/image/avatar1.jpg'
import AVT2 from '../../Assets/image/avatar2.jpg'
import AVT3 from '../../Assets/image/avatar3.jpg'
import AVT4 from '../../Assets/image/avatar4.jpg'

const Testimonials = () => {
  return (
    <section id='testimonials'>
      <div className="title">
        <h5>Review from clients</h5>
        <h2>Testimonials</h2>
      </div>

      <Swiper className="container testimanials-container"
        spaceBetween={40}
        pagination={{ clickable: true }}
        modules={[Pagination]} >
        <SwiperSlide>
          <Testimonial avt={AVT2} name='flan' review='Lorem ipsum dolor sit amet consectetur adipisicing elit. Cum sit quia vero sapiente sunt voluptate.' />
        </SwiperSlide>
        <SwiperSlide>
          <Testimonial avt={AVT3} name='flan' review='Lorem ipsum dolor sit amet consectetur adipisicing elit. Cum sit quia vero sapiente sunt voluptate.' />
        </SwiperSlide>
        <SwiperSlide>
          <Testimonial avt={AVT4} name='flan' review='Lorem ipsum dolor sit amet consectetur adipisicing elit. Cum sit quia vero sapiente sunt voluptate.' />
        </SwiperSlide>
        <SwiperSlide>
          <Testimonial avt={AVT1} name='flan' review='Lorem ipsum dolor sit amet consectetur adipisicing elit. Cum sit quia vero sapiente sunt voluptate.' />
        </SwiperSlide>
      </Swiper>
    </section>
  )
}

export default Testimonials