import {React,useState} from 'react'

const Nav = () => {
  const [activeNav,setactiveNav]=useState('#')
  return (
    <nav>
      <a href='#' onClick={()=>setactiveNav('#')} className={activeNav==='#'? 'active' : ''}><i className='fa fa-home'></i></a>
      <a href='#about' onClick={()=>setactiveNav('#about')} className={activeNav==='#about'? 'active' : ''}><i className='fa fa-user'></i></a>
      <a href='#experience' onClick={()=>setactiveNav('#experience')} className={activeNav==='#experience'? 'active' : ''}><i className='fa fa-book'></i></a>
      <a href='#services' onClick={()=>setactiveNav('#services')} className={activeNav==='#services'? 'active' : ''}><i className='fa fa-rocket'></i></a>
      <a href='#contact' onClick={()=>setactiveNav('#contact')} className={activeNav==='#contact'? 'active' : ''}><i className='	fa fa-address-book-o'></i></a>
    </nav>
  )
}

export default Nav