import React from 'react'
import Portofolio_item from './Portofolio_item'
import porto1 from '../../Assets/image/portfolio1.jpg'
import porto2 from '../../Assets/image/portfolio2.jpg'
import porto3 from '../../Assets/image/portfolio3.jpg'
import porto4 from '../../Assets/image/portfolio4.jpg'
import porto5 from '../../Assets/image/portfolio5.png'
import porto6 from '../../Assets/image/portfolio6.jpg'

const Portofolio = () => {
  return (
    <section id='portofolio'>
      <div className="title">
        <h5>My Recent Work</h5>
        <h2>Portofolio</h2>
      </div>

      <div className="container portofolio-container">
        <Portofolio_item image={porto1} title="Axit tamplate" />
        <Portofolio_item image={porto2} title="noon tamplate" />
        <Portofolio_item image={porto3} title="This is a portofolio item title" />
        <Portofolio_item image={porto4} title="This is a portofolio item title" />
        <Portofolio_item image={porto5} title="This is a portofolio item title" />
        <Portofolio_item image={porto6} title="This is a portofolio item title" />
      </div>
    </section>
  )
}

export default Portofolio