import React from 'react'
import Me from '../../Assets/image/me-about.jpg'
import About_card from './About_card'

const About = () => {
  return (
    <section id='about'>
      <div className='container about-container'>
        <div className='title'>
        <h5>Get to know</h5>
        <h2>About Me</h2>
        </div>
        <div className='content'>
          <div className='img-box'>
            <img src={Me} className='img-fluid' alt='about-image' />
          </div>
          <div>
            <div className='card-component'>
              <About_card icon="fa fa-trophy" title="experinces" discrebtion="3+ Years Working" />
              <About_card icon="fa fa-users" title="Clients" discrebtion="200+ Worldwide" />
              <About_card icon="fa fa-folder" title="Projects" discrebtion="80+ completed" />
            </div>
            <p>Lorem ipsum, dolor sit amet consectetur adipisicing elit. Nulla itaque repellat esse nisi similique error temporibus, incidunt tenetur. Repellat doloribus corporis debitis hic repellendus eveniet.</p>
            <a href='#contact' className='btn btn-primary'>Let's Talk</a>
          </div>
        </div>
      </div>
    </section>
  )
}

export default About