import React,{useRef} from 'react'
import Contact_option from './Contact_option'
import emailjs from 'emailjs-com'

const Contact = () => {

  const form = useRef();

  const sendEmail = (e) => {
    e.preventDefault();

    emailjs.sendForm('service_zd8cpk4', 'template_ioaady8', form.current, 'lnpivEwfAMTW-4tLt')

      .then((result) => {
          console.log(result.text);
      }, (error) => {
          console.log(error.text);
      });
      
      e.target.reset();
  };

  return (
    <section id='contact'>
      <div className='title'>
        <h5>Get in Touch</h5>
        <h2>Contact Me</h2>
      </div>
      <div className='container contact-container'>
          <div className='contact-options'>
            <Contact_option icon="fa fa-envelope contact-icon" name="Email" mail="basharaldaher011@gmail.com" href="mailto:basharaldaher011@gmail.com" />
            <Contact_option icon="fa fa-facebook contact-icon" name="Messenger" mail="Bashar Aldaher" href="https://m.me/bashar.aldaher.21" />
            <Contact_option icon="fa fa-whatsapp contact-icon" name="Whatsapp" mail="+963997796938" href="https://api.whatsapp.com/send?phone=+963996697938" />
          </div>
          <form ref={form} onSubmit={sendEmail}>
            <input type="text" name='name' placeholder='Enter Your Full Name' required />
            <input type="email" name='email' placeholder='Enter Your Email' required />
            <textarea name='message' rows="7" placeholder='Write Your Message Here!' required></textarea>
            <button className='btn btn-primary' type='submit'>Send Message</button>
          </form>
      </div>
    </section>
  )
}

export default Contact